.collectionItem {
  width: 22vw;
  margin-bottom: 20px;
  display: flex;
  flex-direction: column;
  height: 350px;
  align-items: center;
  position: relative;

  &:hover {
    .image {
      opacity: 0.8;
    }
    .btn {
      display: flex;
      position: absolute;
      justify-content: center;
    }
  }

  .image {
    width: 100%;
    height: 95%;
    background-size: cover;
    background-position: center;
    margin-bottom: 5px;
  }

  .btn {
    width: 80%;
    opacity: 0.8;
    position: absolute;
    top: 255px;
    display: none;
    justify-content: center;
  }

  .collectionFooter {
    width: 100%;
    height: 5%;
    display: flex;
    justify-content: space-between;
    font-size: 18px;

    .name {
      width: 90%;
      margin-bottom: 15px;
    }
  }
}
