.header {
  width: 100%;
  height: 120px;
  padding: 0 90px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  position: fixed;
  top: 0;
  right: 0;
  z-index: 9;
  background-color: transparent;
  transition: all 0.5s;

  .item {
    flex: 1;
    display: flex;
    align-items: center;
    justify-content: center;
    padding-left: 150px;
    position: relative;

    &:first-child {
      margin-top: 15px;
      justify-content: flex-end;
    }

    &:nth-child(2) {
      flex: 4;
      justify-content: center;
      // background-color: yellow;
    }

    &:last-child {
      justify-content: flex-end;
    }

    .cart {
      position: relative;
      display: flex;
      align-items: center;
      cursor: pointer;

      button {
        margin: 0 10px;
        height: 30px;
        display: flex;
        align-items: center;
        justify-content: center;
      }
    }
  }
}

.active {
  width: 100%;
  height: 120px;
  padding: 0 90px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  position: fixed;
  top: 0;
  right: 0;
  z-index: 9;
  background-color: white;
  border-bottom: 2px solid rgba(0, 0, 0, 0.1);
  transition: all 0.5s;
}

.callIcon {
  /* background-color: white; */
  /* border-radius: 50%; */
  padding: 10px;
  padding-top: 45px;
  width: 100px;
  height: 100px;
}

.texts {
  margin-left: 20px;
  color: white;

  &:first-child {
    font-size: 12px;
    font-weight: 500;
  }

  &:last-child {
    font-size: 20px;
    font-weight: bold;
  }
}

.list {
  width: 100%;
  padding: 0;
  display: flex;
  align-items: center;
  // justify-content: space-evenly;
  justify-content: flex-end;
  list-style: none;

  .listitem {
    width: 100%;
    margin: 20px;
    cursor: pointer;
  }
}

.counter {
  position: absolute;
  top: -10px;
  right: -10px;
  width: 20px;
  height: 20px;
  border-radius: 50%;
  background-color: white;
  padding: 3px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: bold;
  color: #d1411e;
}

@media screen and (max-width: 480px) {
  .item:nth-child(2) {
    display: none;
  }
  .item:nth-child(1) {
    flex: 3;
  }
}
